import React from "react";

import { HeadFC, PageProps, graphql } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";

import {
  IProjectQueryResult,
  IProjectsPageContext,
} from "../types/project.type";

import ContentContainer from "../components/common/contentContainer";
import ProjectsSection from "../components/projects/projectsSection";
import ScreenshotsCarousel from "../components/projects/screenshotsCarousel";
import { LocalBusinessMetadata } from "../components/common/metadata";

const Project = ({
  data: {
    markdownRemark: { frontmatter, id },
  },
  pageContext: { markupData, lang },
}: PageProps<IProjectQueryResult, IProjectsPageContext>) => {
  const {
    title,
    challenge,
    client,
    clientDescription,
    team,
    screenshots,
    stack,
  } = frontmatter;
  const cover = getImage(frontmatter.cover);
  const clientLogo = getImage(frontmatter.clientLogo);

  return (
    <div itemScope itemType="https://schema.org/CreativeWork">
      {/* BANNER */}
      <div
        className={`w-full bg-secondary-70 min-h-96 flex flex-wrap p-5 md:px-40 justify-evenly items-center`}
      >
        <h2
          className="text-bold text-5xl text-main-100 mb-8 grow-1 basis-full lg:basis-1/2 xl:basis-1/3"
          itemProp="description"
        >
          {title}
        </h2>
        {cover ? (
          <GatsbyImage
            image={cover}
            alt={title}
            className="h-full object-cover"
            itemProp="image"
          />
        ) : null}
      </div>

      {/* CLIENT */}
      {client ? (
        <section className="w-full bg-secondary-100 text-main-100 py-20">
          <ContentContainer className="flex-col items-center">
            <h3 className="text-5xl font-bold pb-6 md:pb-10">
              {markupData.clientSectionTitle}
            </h3>

            <div
              className="flex flex-wrap justify-evenly w-full items-center"
              itemProp="sourceOrganization"
              itemScope
              itemType="https://schema.org/Organization"
            >
              <div className="grow-1 basis-full lg:basis-1/2 xl:basis-1/3">
                {clientLogo ? (
                  <GatsbyImage
                    image={clientLogo}
                    alt={client}
                    className="grayscale"
                    itemProp="logo"
                  />
                ) : null}
              </div>
              <p
                className="basis-full lg:basis-1/2 xl:basis-2/3 text-xl"
                itemProp="description"
              >
                {clientDescription}
              </p>
            </div>
          </ContentContainer>
        </section>
      ) : null}

      {/* CHALLENGE */}
      {challenge?.length ? (
        <section className="w-full bg-main-100 text-secondary-100 py-20">
          <ContentContainer className="flex-col items-center">
            <h3 className="text-5xl font-bold pb-6 md:pb-10">
              {markupData.challengeSectionTitle}
            </h3>

            <ul className="text-2xl leading-7 md:leading-10">
              {challenge.map((challenge, i) => (
                <li key={i} className="list-inside list-disc" itemProp="about">
                  {challenge}
                </li>
              ))}
            </ul>
          </ContentContainer>
        </section>
      ) : null}

      {/* SOLUTION */}
      <section className="w-full bg-secondary-100 text-main-100 py-20 border-b-2 border-secondary-30 border-solid">
        <ContentContainer className="flex-col items-center">
          <h3 className="text-5xl font-bold pb-6 md:pb-10">
            {markupData.solutionSectionTitle}
          </h3>

          {/* team */}
          <h3 className="text-4xl font-bold pt-6 md:pt-10">
            {markupData.teamSectionTitle}
          </h3>
          {team?.length ? (
            <div className="flex flex-wrap justify-center w-full items-center">
              {team.map((member) => (
                <div
                  key={member}
                  className="team-member w-56 h-36 border-2 border-solid border-secondary-30 m-4 p-4 text-2xl flex justify-center items-center relative transition-all duration-500 hover:text-secondary-100"
                >
                  <p className="w-40 text-center z-[1]" itemProp="contributor">
                    {member}
                  </p>
                  <div
                    className={`line w-0 h-full bg-main-100 absolute top-1/2 right-0 -translate-y-1/2 transition-all duration-500`}
                  ></div>
                </div>
              ))}
            </div>
          ) : null}

          {/* stack */}
          <h3 className="text-4xl font-bold pt-6 md:pt-10">
            {markupData.techSectionTitle}
          </h3>
          <div className="flex flex-wrap justify-center w-full items-center">
            {stack.map((tech) => (
              <div
                key={tech}
                className="applied-tech w-56 h-36 border-2 border-solid border-secondary-30 m-4 p-4 text-2xl flex justify-center items-center relative"
              >
                <p className="w-40 text-center" itemProp="keywords">
                  {tech}
                </p>
                <div
                  className={`line w-1 h-0 bg-main-100 absolute top-1/2 right-0 -translate-y-1/2 transition-all duration-500`}
                />
              </div>
            ))}
          </div>
        </ContentContainer>
      </section>

      {/* RESULT */}
      {screenshots?.length ? (
        <section className="w-full bg-secondary-100 text-main-100 py-20">
          <ContentContainer className="flex-col items-center">
            <h3 className="text-5xl font-bold pb-6 md:pb-10">
              {markupData.resultSectionTitle}
            </h3>

            <ScreenshotsCarousel screenshots={screenshots} />
          </ContentContainer>
        </section>
      ) : null}

      <ProjectsSection lang={lang} excludeId={id} />
      <LocalBusinessMetadata itemProp="author" />
    </div>
  );
};

export default Project;

export const Head: HeadFC<IProjectQueryResult, IProjectsPageContext> = ({
  data: {
    markdownRemark: {
      frontmatter: { title },
    },
  },
  pageContext,
}) => (
  <title>
    {pageContext.markupData.head}: {title}
  </title>
);

export const query = graphql`
  query ($id: String) {
    markdownRemark(id: { eq: $id }) {
      id
      frontmatter {
        stack
        title
        challenge
        client
        clientDescription
        clientLogo {
          childImageSharp {
            gatsbyImageData(
              formats: [AUTO, WEBP, AVIF]
              placeholder: BLURRED
              height: 150
            )
          }
        }
        cover {
          childImageSharp {
            gatsbyImageData(
              formats: [AUTO, WEBP, AVIF]
              placeholder: BLURRED
              height: 400
            )
          }
        }
        team
        screenshots {
          id
          childImageSharp {
            gatsbyImageData(
              formats: [AUTO, WEBP, AVIF]
              placeholder: BLURRED
              height: 600
            )
          }
        }
      }
    }
  }
`;
