import React from "react";

import { graphql, useStaticQuery } from "gatsby";

import {
  IProjectPreviewQueryResult,
  IProjectSectionQueryResult,
} from "../../types/project.type";

import ProjectPreview from "./projectPreview";
import TemplateSection from "../common/templateSection";
import { ILang } from "../../types/common.type";

interface IProjectsSectionProps extends ILang {
  excludeId?: string;
}

const ProjectsSection = ({ excludeId, lang }: IProjectsSectionProps) => {
  const projectsPreviewData = useStaticQuery(
    query
  ) as IProjectSectionQueryResult;

  const sectionMarkup = projectsPreviewData?.markupData?.nodes?.find(
    (node) => node.fields.language === lang
  )?.frontmatter?.projects;

  let projects: IProjectPreviewQueryResult[] | null | undefined;
  if (lang in projectsPreviewData)
    projects = projectsPreviewData?.[lang]?.nodes as
      | IProjectPreviewQueryResult[]
      | null
      | undefined;
  else projects = projectsPreviewData.en?.nodes;

  if (!projects) return <></>;

  if (excludeId) {
    projects = projects.filter((project) => project.id !== excludeId);
  }

  return (
    <TemplateSection
      section="projects"
      title={sectionMarkup?.moreProjectsSectionTitle ?? "OUR PROJECTS"}
      linkTitle={sectionMarkup?.moreProjectsSectionLinkTitle ?? "SEE MORE"}
      className="bg-main-100 text-secondary-100"
    >
      <div className="flex justify-between flex-col items-center sm:items-stretch sm:flex-row">
        {projects.map((project, i) => {
          if (i === 3) return;
          return (
            <ProjectPreview
              key={project.id}
              project={project}
              theme="light"
              className="basis-[48%] sm:last:hidden md:last:flex md:basis-[31%] mt-16 sm:mt-0"
            />
          );
        })}
      </div>
    </TemplateSection>
  );
};

export default ProjectsSection;

export const query = graphql`
  {
    en: allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "projects" }, language: { eq: "en" } }
      }
      sort: { frontmatter: { date: DESC } }
      limit: 4
    ) {
      nodes {
        id
        frontmatter {
          title
          cover {
            childImageSharp {
              gatsbyImageData(
                width: 600
                formats: [AUTO, WEBP, AVIF]
                placeholder: BLURRED
              )
            }
          }
        }
      }
    }
    ru: allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "projects" }, language: { eq: "ru" } }
      }
      sort: { frontmatter: { date: DESC } }
      limit: 4
    ) {
      nodes {
        id
        frontmatter {
          title
          cover {
            childImageSharp {
              gatsbyImageData(
                width: 600
                formats: [AUTO, WEBP, AVIF]
                placeholder: BLURRED
              )
            }
          }
        }
      }
    }
    markupData: allMarkdownRemark(
      filter: { fields: { sourceName: { eq: "common" } } }
      sort: { frontmatter: { order: ASC } }
    ) {
      nodes {
        fields {
          language
        }
        frontmatter {
          projects {
            moreProjectsSectionTitle
            moreProjectsSectionLinkTitle
          }
        }
      }
    }
  }
`;
