import React, { PropsWithChildren } from "react";
import { Link } from "gatsby";

import ContentContainer from "./contentContainer";

const TemplateSection = ({
  section,
  title,
  linkTitle,
  className,
  fullWidth,
  children,
}: PropsWithChildren<{
  title: string;
  linkTitle?: string;
  section?: string;
  className?: string;
  fullWidth?: boolean;
}>) => (
  <section className={`w-full py-48 px-6 ${className}`}>
    <ContentContainer className="flex-col items-center" fullWidth={fullWidth}>
      <div className="flex justify-between w-full items-center pb-10 md:pb-16">
        <h3 className="text-4xl md:text-5xl font-black w-full max-w-7xl mx-auto pr-8">
          {title.toUpperCase()}
        </h3>
        {section ? (
          <Link
            to={`/${section}`}
            className="text-end text-2xl md:text-3xl text-secondary-10 hover:text-secondary-100 font-normal cursor-pointer whitespace-nowrap"
          >
            {linkTitle ? linkTitle.toUpperCase() : "SEE MORE"} &#8594;
          </Link>
        ) : null}
      </div>
      {children}
    </ContentContainer>
  </section>
);

export default TemplateSection;
